<template>
  <div id="app" class="container">
    <h1>Weather App Cards</h1>
    <p>Search by city and save/remove cards to local storage</p>
    <header>
      <input @keyup="getCities($event.target.value)" type="text" v-model="city">
      <div id="results">
        <ul v-if="showResults">
          <li v-for="city in cityResults" :key="city.name" @click="fetchWeather(city.name)">
            {{ city.name }}
          </li>
        </ul>
      </div>
    </header>
    <section>
      <transition name="result">
      <div class="weather-card" @click="saveWeatherData(weatherData)"
      v-if="showInfo">
        <div class="card-header">
          <h1>{{ weatherData.city }}, {{ weatherData.country }}</h1>
        </div>
        <div class="card-body">
          <i :class="weatherData.icon"></i>
          <p>{{ weatherData.forecast }}</p>
        </div>
        <div class="card-footer">
          <h1 v-html="`${weatherData.temp}&deg;`"></h1>
        </div>
        <div class="weather-card-modal">
          <h3>Save</h3>
        </div>
      </div>
      </transition>
    </section>
    <section>
      <div class="weather-board" v-if="cities.length > 0">
        <transition-group name="list" tag="ul" class="list-ul">
          <li v-for="(item,index) in cities" :key="item.city">
            <div class="weather-card" @click="removeWeatherData(index)" :style="{backgroundColor: item.bgColor}">
              <div class="card-header">
                <h1>{{ item.city }}, {{ item.country }}</h1>
              </div>
              <div class="card-body">
                <i :class="item.icon"></i>
                <p>{{ item.forecast }}</p>
              </div>
              <div class="card-footer">
                <h1 v-html="`${item.temp}&deg;`"></h1>
              </div>
              <div class="weather-card-modal delete">
                <h3>Remove</h3>
              </div>
            </div>
          </li>
        </transition-group>
      </div>
      <div id="message" v-else>
        <h3>No weather items to show.<br> Add an item using the search box above</h3>
      </div>
    </section>
  </div>
</template>

<script>
  const storage = require('../static/storage');

  import { Observable } from 'rxjs/Observable';
  import 'rxjs/add/observable/from';
  import 'rxjs/add/operator/filter';
  import 'rxjs/add/operator/debounceTime';
  import 'rxjs/add/operator/distinctUntilChanged';
  import axios from 'axios';

export default {
  name: 'app',
  data () {
    return {
      apikey: '579dc9e1b9185e8312a5973983e5b62e',
      weatherData: {},
      city: '',
      cityResults: [],
      cities: [],
      showResults: false,
      showInfo: false,
      bkg: []
    }
  },
  methods: {

    getCities(search) {
      let obs = Observable.create((observer) => {
        observer.next(search);
      });
      obs
        .filter(text => text.length > 2)
        .debounceTime(200)
        .distinctUntilChanged()
        .subscribe(query => {
          return axios.get('http://autocomplete.wunderground.com/aq?query=' + query)
            .then(res => {
              this.showResults = true;
              this.cityResults = res.data.RESULTS;
            })
            .catch((error) => console.log(error));
        });
    },

    fetchWeather(location) {
      return axios({
        method: 'get',
        url: 'http://api.openweathermap.org/data/2.5/weather',
        params: {
          q: location,
          APPID: this.apikey
        }
      })
        .then((res) => {
          this.weatherData = {
            city: res.data.name,
            country: res.data.sys.country,
            temp: this.convertToF(res.data.main.temp),
            forecast: res.data.weather[0].description,
            icon: this.iconClass(res.data.weather[0].icon)
          }
          this.showResults = false;
          this.showInfo = true;
          this.city = location;
        })
        .catch((err) => console.log(err));
    },

    iconClass(text) {
      return 'owi owi-' + text;
    },

    convertToF(k) {
      return Math.round(((k - 275.15) * 1.8));
    },

    saveWeatherData(data) {
      data.bgColor = this.setRandomColor();
      this.cities.push(data);
      this.city = '';
      this.showInfo = false;
    },

    removeWeatherData(index) {
      this.cities.splice(index, 1);
    },

    setRandomColor() {
        var letters = 'BCDEF'.split('');
        var color = '#';
        for (var i = 0; i < 6; i++ ) {
            color += letters[Math.floor(Math.random() * letters.length)];
        }
        return color;
    },

    refreshForecast() {
      let cities = storage.getCities();
      
      cities.forEach((item) => {
        return axios({
          method: 'get',
          url: 'http://api.openweathermap.org/data/2.5/weather',
          params: {
            q: item.city,
            APPID: this.apikey
          }
        })
        .then(res => {
          this.cities.temp = this.convertToF(res.data.main.temp),
          this.cities.forecast = res.data.weather[0].description,
          this.cities.icon =  this.iconClass(res.data.weather[0].icon)
        });
      });
    }
  },

  created() {
   if(storage.checkStorage()){
      this.cities = storage.getCities();
    } else {
      this.cities = [];
    }
  },

  mounted() {
    this.refreshForecast();
  },
 
  updated() {
    storage.saveCities(this.cities);
    this.refreshForecast();
  }
}
</script>

<style>

  /* 
    sun: #F2B900
   */

  @import url('https://fonts.googleapis.com/css?family=Lato');
  @import url('../static/css/open-weather-icons.css');

  #app {
    font-family: 'Lato', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  body {
    margin: 0;
    padding: 0;
    background-color: #FFFAF4;
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  header {
    width: 400px;
    padding: 1em;
    position: relative;
  }
  header input {
    width: 100%;
    height: 20px;
    text-indent: 5px;
  }
  #results {
    width: 93%;
    position: absolute;
    z-index: 10;
  }
  #results ul {
    text-align: left;
    margin: 0;
  }
  #results li {
    padding: 5px;
    background-color: #FCF2E3;
  }
  #results li:hover {
    background-color: #F7E8D7;
    cursor: pointer;
  }

  .container {
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  .weather-card {
    width: 170px;
    height: 270px;
    padding: 1em;
    background-color: #F2B900;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
    cursor: pointer;
  }
  .weather-card-modal {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 20;
    width: 100%;
    height: 100%;
    background: rgba(12,201,43,0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFF;
    transition: top .3s;
  }
  .delete {
    background: rgba(255,12,49,0.6);
  }
  .weather-card:hover > .weather-card-modal {
    top: 0;
  }
  .card-header h1 {
    font-size: 1.2em;
  }
  .card-body p {
    text-transform: uppercase;
    font-size: .7em;
  }
  .card-body i {
    font-size: 3em;
  }
  .card-footer h1 {
    font-size: 1.8em;
  }

  .weather-board {
    width: 100%;
    max-width: 800px;
    padding: 1em;
  }
  .weather-board ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
  }

/* result transition */

  .result-enter-active,
  .result-leave-active {
    transition: opacity .7s, transform .7s;
  }
  .result-enter,
  .result-leave-to {
    opacity: 0;
    transform: translateY(-10px);
  }

/* weatherboard transition */
  .list-ul {
    transition: all .7s;
  }
  .list-enter-active,
  .list-leave-active {
    transition: opacity .7s, transform .7s;
  }
  .list-enter,
  .list-leave-to {
    opacity: 0;
    transform: translateY(-10px);
  }

</style>



